import React, { useState, useEffect } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { dbService } from '../fbase';
import {
  collection,
  addDoc,
  query,
  onSnapshot,
  doc,
  orderBy,
} from 'firebase/firestore';
import styles from './css/Write.module.css';
import like from '../assets/like.png';
import dislike from '../assets/dislike.png';
import { faL } from '@fortawesome/free-solid-svg-icons';

const Write = ({ userObj }) => {
  const { state } = useLocation();
  const [title, setTitle] = useState('');
  const dateObj = new Date();
  const today = `${dateObj.getFullYear()}/${dateObj.getMonth()}/${dateObj.getDate()}`;
  const [review, setReview] = useState(''); //review , form
  const [reviews, setReviews] = useState([]); //review  
  const [recommend, setRecommend] = useState(true);
  console.log(state);
  const navigate = useNavigate();

  useEffect(() => {
    // review firesotre   .      ... nweete   
    const q = query(
      collection(dbService, 'reviews'), //collection "reviwes" 
      orderBy('createdAt', 'desc') //
    );
    onSnapshot(q, (snapshot) => {
      //     
      const reviewArr = snapshot.docs.map((document) => ({
        id: document.id,
        ...document.data(),
      }));
      setReviews(reviewArr); //  state   
    });
  }, []);

  const onSubmit = (e) => {
    e.preventDefault();
    const title = e.target[0].value;
    const date = e.target[1].value;
    const star = e.target[2].value;
    const review = e.target[3].value;
    const bookimg = state.cover;
    const author = state.author;
    //reviews  doc    userObj.uid   ?
    const docRef = addDoc(collection(dbService, 'reviews'), {
      text: review, // 
      createdAt: dateObj, //  
      creatorId: userObj.uid, //   
      title: title, // 
      star: star, //  
      bookimg: bookimg, //  
      author: author, //
      CID: state.categoryId, //  
    });
    setReview('()'); //    .   ""  
    navigate('/library/*');
  };

  const onChange = (e) => {
    const {
      target: { value, name },
    } = e;
    if (name === 'title') {
      setTitle(value);
    }
    if (name === 'review') {
      setReview(value); // 
    }
  };

  /*
  const onSetRecommend = (e) => {
    const recommend = document.querySelector('.recommend');
    const unrecommend = document.querySelector('.unrecommned');
    if (e.target.name === 'recommend') {
      setRecommend(true);
      if (!recommend.classList.contains('setRecommend')) {
        recommend.classList.add('setRecommend');
      }
      unrecommend.classList.remove('setUnRecommned');
    }
    if (e.target.name === 'unrecommned') {
      setRecommend(false);
      recommend.classList.remove('setRecommend');
      unrecommend.classList.add('setUnRecommned');
    }
  };
  */

  return (
    <div className={styles.wrapper}>
      <section>
        <div className={styles.sectionTop}>
          <img src={state.cover} className={styles.coverImg} />
          <h2>{state.title}</h2>
          <span> {state.author}</span>
        </div>
        <div className={styles.sectionBottom}>
          <div>{state.description}</div>
        </div>
      </section>

      <form id="writeForm" onSubmit={onSubmit}>
        <div className={styles.title}>
          <label for="title"> </label>
          <input
            type="text"
            placeholder={state ? `${state.title}` : ''}
            value={state ? `${state.title}` : `${title}`}
            onChange={onChange}
            name="title"
            id="title"
          ></input>
        </div>

        <div className={styles.title}>
          <label for="date"></label>
          <input type="date" name="date" id="date"></input>
        </div>

        <div className={styles.title}>
          <label for="star"></label>
          <select name="star">
            <option value=""></option>
            <option value="1">1</option>
            <option value="2">2</option>
            <option value="3">3</option>
            <option value="4">4</option>
            <option value="5">5</option>
          </select>
        </div>

        <div className={styles.title}>
          <label for="review"></label>
          <textarea name="review" form="writeForm"></textarea>
        </div>

        <input className={styles.Btn} type="submit" value=""></input>
      </form>
    </div>
  );
};

/*      <div className={styles.recommendSection}>
<label>Recommend</label>
<img
  src={like}
  className={`${styles.recommend} ${styles.Btn2} ${styles.setRecommend}`}
  name="recommend"
/>
<img
  src={dislike}
  className={`${styles.unrecommend} ${styles.Btn2}`}
  name="unrecommend"
/>
</div>
*/
export default Write;
