import React, { useEffect, useState } from "react";
import { Link, useNavigate, useLocation } from "react-router-dom";
import { getAuth } from "firebase/auth";
import { dbService } from "../fbase";
import { doc, setDoc } from "firebase/firestore";

const $ = (type) => {
  return document.querySelector(type);
};

const SelectGenre = () => {
  const navigate = useNavigate();
  const [selected, setSelected] = useState([]); //      : selected
  const auth = getAuth();
  //console     
  useEffect(() => {
    const userRef = doc(dbService, "UserInfo", `${auth.currentUser.uid}`);
    setDoc(userRef, { genre1: 0, genre2: 0, genre3: 0, genre4: 0 });
    console.log(selected);
  }, [selected]);

  //    localStorage  
  // main  .
  // Submit localStorage. ~~~   firebase    .

  const onSubmit = (event) => {
    event.preventDefault();
    localStorage.setItem("selected", JSON.stringify(selected));
    navigate("/dogsbooks");
  };
  //      selected  ,
  //   
  const onChecked = (event) => {
    let checked = event.target.checked;
    let value = event.target.value;
    if (checked) {
      setSelected([...selected, value]);
    } else {
      let filtered = selected.filter((element) => element !== value);
      setSelected(filtered);
    }
  };
  return (
    <div>
      <h1></h1>
      <form method="post">
        <div>
          <input
            type="checkbox"
            id="checkbox"
            value="koreanNovel"
            onClick={onChecked}
          />
          
        </div>
        <div>
          <input
            type="checkbox"
            id="checkbox"
            value="Humanities"
            onClick={onChecked}
          />
          
        </div>
        <div>
          <input
            type="checkbox"
            id="checkbox"
            value="science"
            onClick={onChecked}
          />
          
        </div>
        <div>
          <input
            type="checkbox"
            id="checkbox"
            value="economics"
            onClick={onChecked}
          />
          /
        </div>
        <input type="submit" value={""} onClick={onSubmit}></input>
      </form>
    </div>
  );
};

export default SelectGenre;
